import axios from 'axios';

export const allTodos = async () => {
    try {
        const response = await axios.get("http://localhost:5000/api/todos");
        const data = response.data;
        // return data.map(todo => ({
        //     todo,
        //     createdAt: new Date(todo.createdAt)
        // }))

        return data;


    } catch (err) {
        console.log(err.message);
    }
}

export const addTodo = async (todo) => {
    try {

        return await axios.post("http://localhost:5000/api/todos", todo );

    } catch (error) {
        console.log(error);
    }
}

export const updateTodo = async (id) => {
    try {
        return await axios.put(`http://localhost:5000/api/todos/${id}`, {todo: "TEST"})
    } catch (error) {
        console.log(error);
    }
}

export const deleteTodo = async (id) => {
    try {
        return await axios.delete(`http://localhost:5000/api/todos/${id}`)
    } catch (error) {
        console.log(error);
    }
}