<template>
  <div class="container">
    <h1 class="display-4">{{ msg }}</h1>
    <div class="todos-section">
      <div class="add-todo">
        <div class="row">
          <div class="col-10">
            <input type="text" @keyup.enter="newTodo" v-model="todo" id="todo" class="form-control mb-3">
          </div>
          <div class="col-2">
            <button @click="newTodo" class="btn btn-primary">Add Todo</button>
          </div>
        </div>
      </div>

      <div class="todo" v-for="(item, index) in todos" :key="item._id">
        <p class="todo-text"> <span class="badge badge-primary">{{ index + 1 }}</span> {{ item.todo }}</p>
        <button @click="delTodo(item._id)" class="btn btn-danger" v-if="todos.length">Delete</button>
      </div>
    </div>

  </div>
</template>

<script>
import { allTodos, addTodo, updateTodo, deleteTodo } from "../TodoService";
export default {
  name: 'TodoComponent',
  props: {
    msg: String
  },
  data() {
    return {
      todos: [],
      error: '',
      todo: ''
    }
  },
  async mounted() {
    try {
      this.todos = await allTodos();
    } catch (err) {
      this.error = err;
    }
  },

  methods: {
    // Create New Todo
    async newTodo() {

      try {
        const response = await addTodo({
        todo: this.todo
      });
        this.todos.push(response.data);
        this.todo = "";
      } catch (error) {
        console.log(error);
      }
    },

    // update todo
    async updateTodo(id) {
      try {
        const response = await updateTodo(id);
        console.log("UPDATE:", response.data);
      } catch (error) {
        console.log(error);
      }
    },

    // delete post
    async delTodo(id) {
      try {
        const item = await deleteTodo(id);
        this.todos.splice(item, 1)

      } catch (error) {
        console.log(error);
      }
    }
  }
}
</script>

